.sale-content {
  .sale-header {
    margin: 48px 0px 24px;
    display: flex;
    align-items: center;
    .sale-title {
      font-size: 28px;
      text-transform: uppercase;
      font-weight: bold;
      color: #fc521d;
    }
    .time-sale {
      margin-left: 24px;
      .time-item {
        font-size: 18px;
        background: #000;
        padding: 4px 11px;
        color: #fff;
        margin: 0 4px;
        border-radius: 5px;
      }
    }
  }
  .sale-container {
    background: #fff;
    padding: 20px;
    box-shadow: 0 4px 6px #00000029;
    border-radius: 8px;
    .react-multiple-carousel__arrow--left {
      position: absolute;
      left: 0;
    }
    .react-multiple-carousel__arrow--right {
      position: absolute;
      right: 0;
    }
    .sale-product-link{
      text-decoration: none;
      .sale-product {
        width: 220px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 8px;
        cursor: pointer;
        .sale-img {
          width: 180px;
        }
        .sale-product-name {
          font-weight: bold;
          font-size: 14px;
          text-align: center;
          height: 36.8px;
          color: #000;
        }
        .sale-product-price {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-evenly;
          .sale-product-new-price,
          .sale-product-old-price {
            margin: 0;
          }
          .sale-product-old-price {
            text-decoration: line-through;
            font-size: 15px;
            color: #333;
          }
          .sale-product-new-price {
            font-weight: bold;
            font-size: 18px;
            color: #fd475a;
          }
        }
      }
    }
  }
}
